body{
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
    padding: 0;
}
.container{
    background-color: darkcyan;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
}
h2{
    text-align: center;
    font-size: 24px;
    margin-bottom: 10px;
}
input{
    width: 240px;
    height: 25px;
}
label{
    display: block;
}
button{
    background-color: red;
    color: white;
    margin-top: 20px;
    padding: 8px;
    border-radius: 5px;
}